import React, { useState } from 'react';
import "./ListView.css"
import pginfo from  "./pginfo.json"
import gnrhinfo from  "./gnrhinfo.json"
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from '@material-ui/core/Checkbox';

const ListView = props => {
    const { ListOfInstrucitons, DateToStartBreeding, SynchronizationProtocol, SystemType, GNRH, PG} = props;
    const [showTimes, setShowTimes] = useState(false)
    const [showMilitaryTime, setShowMilitaryTime] = useState(false)

    const handleMilitaryCheckbox = (checked) => {
        setShowMilitaryTime(checked)
    }
    
    const handleTimeCheckbox = (checked) => {
        setShowTimes(checked)
    }

    //Legibility edits
    const leadingZeroesHours = (Hours) =>{
        return (Hours < 10 ? '0' : '') + Hours;
    }

    //Legibility edits
    const leadingZeroesMinutes = (Minutes) =>{
        return (Minutes < 10 ? '0' : '') + (Minutes);
    }

    //String replace for PG's
    const addPGtoSteps = (text) =>{
        if(text === undefined){return ''}
        return(text.replace("${PG}", pginfo[PG]))
    }
    
    //String replace for GNRH's
    const addGNRHtoSteps = (text) =>{    
        if(text === undefined){return ''}    
        return(text.replace("${GNRH}", gnrhinfo[GNRH]))
    }

    const getClockHours = (hours) =>{
        if(hours > 12){
            return (hours % 12);
        }
        return hours
    }

    //String.Includes() looks for TimeEdit code in JSON protocol instructions and returns the appropriate time
    const addTimedEventstoSteps = (text, dateObject) =>{    
        if(text === undefined){return ''}   
        if(text.includes("${Time-Edit}")){
            
            let plusTimeEdit = text.indexOf(":+:");
            let timeAdjustment = undefined;
          
            //if you found where the text needs to be edited
            if (plusTimeEdit !== -1){
               ///the digits that weill need to added or subtraced
                let analysis = text.substring(plusTimeEdit + 3)

                timeAdjustment = dateObject.getHours() + parseInt(analysis);

                if(!showMilitaryTime){
                    return ( getClockHours(timeAdjustment).toString()) + ':' + leadingZeroesMinutes(dateObject.getMinutes())
                }
                else{
                    return (`${leadingZeroesHours(timeAdjustment)}${leadingZeroesMinutes(dateObject.getMinutes())}`)
                }
            }
        }
        return(text)
    }

    

    return (
        <>
        <br/>
            <h2>Protocol #{SynchronizationProtocol}</h2>
            <div className="noprint">
                {!(SystemType === 1) && <>
                    <FormControl variant='outlined' >
                    <FormControlLabel 
                    control={
                        <Checkbox 
                            onChange={event => {
                                handleTimeCheckbox(event.target.checked)
                            }}
                            defaultChecked = {showTimes}
                        />}
                    label="Hide Times" />
                </FormControl>
                <br />
                <br />
                </>
                }
                
                
                <FormControl variant='outlined' >
                    <FormControlLabel 
                        control={
                            <Checkbox 
                                onChange={event => {
                                handleMilitaryCheckbox(event.target.checked)
                            }}
                            defaultChecked = {showMilitaryTime}
                        />}
                        label="Convert Time to Military" />
                </FormControl>
                <br />
                <br />
            </div>
            
            <div className="centerTable">
                <table >
                    <tr>
                        <th>Date and Time</th>
                        <th>Instructions</th>
                    </tr>
                    {ListOfInstrucitons.map(instruction => {
                        let tempDate = new Date(DateToStartBreeding)
                        tempDate.setDate(DateToStartBreeding.getDate() + parseInt(instruction.OnDay))
                        return(
                            <tr >
                                <td> {tempDate.getMonth() + 1} / { tempDate.getDate() } / { tempDate.getFullYear()} 
                                <br/>
                                    {!(SystemType === 1) &&
                                    !(instruction.step1 === "21 Days since last PG") && 
                                    !(instruction.step1 === "Expected Birth Date") &&
                                     !(instruction.step1 === "Bull Turn In Begins") && <>
                                        {!showTimes ?
                                        <> 
                                            {!showMilitaryTime ? 
                                            <>
                                            {getClockHours(tempDate.getHours()).toString() }:{leadingZeroesMinutes(tempDate.getMinutes())} {(tempDate.getHours() > 11)? "PM" : "AM"}
                                            </>
                                                :
                                            leadingZeroesHours(tempDate.getHours()).toString() + leadingZeroesMinutes(tempDate.getMinutes()).toString()
                                            }
                                        </>
                                        :
                                        <></>} 
                                    </>
                                    }
                                                            
                                </td>
                                <td>
                                    <br/>
                                        {addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step1)), tempDate )}<br/>
                                        {addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step2)), tempDate )}<br/>
                                        {addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step3)), tempDate )}<br/>
                                        {addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step4)), tempDate )}<br/>
                                        {addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step5)), tempDate )}<br/>
                                        {addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step6)), tempDate )}<br/>
                                    <br/>
                                </td>
                            </tr>
                        )
                    })} 
                </table>
            </div>
        </>
    );
}

export default ListView;