import React from 'react';
import CowOrHeiferData from "../InitialData/CowOrHeifer.json"
import SemenTypeData from "../InitialData/SemenType.json"
import BreedTypeData from "../InitialData/BreedType.json"
import SystemTypeData from "../InitialData/SystemType.json"

const InputSummary = props => {
    const { BreedType, CowOrHeifer, SemenType, SystemType } = props;

    function getKeyByValue(object, value) {
        return Object.keys(object).find(key => object[key] === value);
      }


    return (
        <>
         <div className="centerDiv" style={{marginBottom:'20px'}}>
            <table>
                <tr>
                    <th>Input Name</th>
                    <th>Selection</th>
                </tr>
                
                <tr>
                    <td>
                    Cow  or Heifer
                    </td>
                    <td>
                        {getKeyByValue(CowOrHeiferData, CowOrHeifer)}
                    </td>
                </tr>

                <tr>
                    <td>
                    Semen Type
                    </td>
                    <td>
                    {getKeyByValue(SemenTypeData, SemenType)}

                    </td>
                </tr>

                <tr>
                    <td>
                    Breed Type 
                    </td>
                    <td>
                        {getKeyByValue(BreedTypeData, BreedType)}
                    </td>
                </tr>

                <tr>
                    <td>
                    System Type 
                    </td>
                    <td>
                        {getKeyByValue(SystemTypeData, SystemType)}
                    </td>
                </tr>
                                
            </table>
            
            </div>

        
        </>
    );
}

export default InputSummary